import { firebaseClient } from '../base44Client.js';

export const list = (options = {}) => {
  const { sort = '-createdAt', limit = null } = options;
  return firebaseClient.entities.Notification.list(sort, limit);
};

export const fetchNotifications = () => {
  return list();
};

export const filter = (filters = {}, limit = null) => firebaseClient.entities.Notification.filter(filters, limit);

export const create = (data) => firebaseClient.entities.Notification.create(data);

export const update = (id, data) => firebaseClient.entities.Notification.update(id, data);

export const remove = (id) => firebaseClient.entities.Notification.delete(id);